import React, { useEffect, useState } from 'react';
import { invoke } from '@forge/bridge';

function App() {
  const [data, setData] = useState(null);

  useEffect(() => {
    fetchSSR();
  }, []);

  // This you can replace with a Forge remote
  // https://developer.atlassian.com/platform/forge/remote/calling-from-frontend/
  const fetchSSR = () => invoke('getHTML', { example: 'example-payload' }).then(setData);

  return (
    <div>
      <button onClick={fetchSSR}>Reload</button>
      <div dangerouslySetInnerHTML={{__html: data}}></div>
    </div>
  );
}

export default App;
